body {
  background-color: rgb(255, 255, 255);
  transition: background-color 0.5s;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.circle{
  width: 55px;
  height: 55px;
  border: 2px solid black;
  border-radius: 50%;
  background-color: transparent;
  animation :rotate 2.5s ease-in ;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: -250px;
  margin: auto;
  opacity: 1;
}
.page{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page>h1{
  
  animation: come 5s  linear;
}
@keyframes come {
  0%{
    margin-left: -50px;
    opacity: 0;
  }
  50%{
    opacity: 0;
  }
}

@keyframes rotate{
    0%
    {
        -webkit-transform: rotate(0);
                transform: rotate(0);
                border-top: transparent;
                border-left: transparent;
                border-bottom: transparent;
                left: 100px;
              }

              80%
              {
                -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
                border-bottom: 2px solid black;
                
    }
    90%{
      transform: scale(1.3);
    }
    100%{
      transform: scale(1);
      border-left: 2px solid black;
    }
}
.eyecont{
   width: 40px;
   height:30px;
   display: flex;
   position: relative;
   justify-content: space-between;
   padding-top: 5px;
}
.eye{
  width: 10px;
  height: 30px;
  margin-top: 5px;
  border: 4px solid black;
  border-bottom: none;
  border-left: none;
  border-right: none;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  animation: face 2.5s linear;
  visibility: visible;
  transition: visibility 2s;
}
.mouth{
  width: 40px;
  height: 30px;
  margin-top: -15px;
  border: 2px solid black;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  animation:face 2.5s linear;
  visibility: visible;
  transition: visibility 2s;
}
@keyframes face {
  from{
    visibility: hidden;
  }
  to{
    visibility: hidden;
  }
}
.pagename{
 display: flex;
 justify-content: space-between;
 width: 100%;
 max-width: 1024px;
}

.heading{
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.212);
  background-color: rgb(255, 255, 255);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  top: 0;
}

*{
  margin: 0%;
  padding: 0;
}
.name{
  font-size: 25px;
  display: flex;
  font-weight: bolder;
  align-items: center;
  color: rgb(242,50,4);
  padding: 0px 0px 0px 20px ;
}
nav{
  display: flex;
  align-items: center;
  width: 100%;
  overflow: scroll;
  overflow-y: hidden;
  color: rgb(242,50,4);
  margin: 10px 0px 0px 0px ;
  max-width: 1024px;
}
::-webkit-scrollbar {
  width: 5px;
  height: 3px;
}
::-webkit-scrollbar-track {
  background:rgb(255, 255, 255);
}
::-webkit-scrollbar-thumb {
  background: rgb(51, 50, 50);
  border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
a{
  padding: 10px 20px 10px 20px ;
  text-decoration: none;
  transition: 0.5s;
  cursor: pointer;
  color: rgb(0, 0, 0);
}
a:hover{
  color: rgb(242,50,4);
}


.news{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  width: 100%;
}
.mainnews{
  display: flex;
  width: 80%;
  max-height: 400px;
  max-width: 1024px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 70px 0px 10px 0px;
  cursor: pointer;
}
.newspics{
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  margin: 0px 15px 0px 0px;
  width:40%;
  height: auto;
}
.mainnews > hr{
margin: 40px 0px 20px 0px ;
background-color: rgba(98, 97, 97, 0.322);
border-color:rgb(93, 93, 93);
border: none; 
height:1px ;
}
.descriptions{
display: flex;
flex-direction: column;
justify-content: space-between;
}
.moredescriptions{
color: rgba(0, 0, 0, 0.75);
}
.type{
  padding: 10px 0px 0px 0px;
  color: black;
}
.title{
  color: black;
}
.category{
  display: flex;
  justify-content: space-between;
}
.author{
  color:rgb(242,50,4) ;
  padding: 10px 0px 0px 0px;
}
.modes{
  width: 40px;
  height: 17px;
  border-radius: 10px;
  border: 1px solid black;
  margin: 15px 10px 0px 0px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.switch{
  height: 15px;
  width: 15px;
  margin-left: 3px;
  /* background-color: rgb(0, 0, 0); */
  border-radius: 10px;
  transition: 0.4s;
}
.breakingnews{
  display: flex;
  width: 100%;
  overflow: scroll;
  overflow-y: hidden;
  margin: 80px 0px 40px 0px ;
  max-width: 1024px;
  cursor: pointer;
}
.secondpart{
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: 0px 10px 0px 10px;
}
.oppsy{
  max-width: 1024px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.oppsy h2{
  color:  #f23204;
  
}
.not-found{
  width: 100%;
  height: 400px;
  animation: tada 4.5s ease infinite;
  z-index: -10;
}
@keyframes tada
{
    from
    {
        -webkit-transform: scale3d(1, 1, 1);
                transform: scale3d(1, 1, 1);
    }

    10%,
    20%
    {
        -webkit-transform: scale3d(.95, .95, .95) rotate3d(0, 0, 1, -5deg);
                transform: scale3d(.95, .95, .95) rotate3d(0, 0, 1, -5deg);
    }

    30%,
    50%,
    70%,
    90%
    {
        -webkit-transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 5deg);
                transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 5deg);
    }

    40%,
    60%,
    80%
    {
        -webkit-transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, -5deg);
                transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, -5deg);
    }

    to
    {
        -webkit-transform: scale3d(1, 1, 1);
                transform: scale3d(1, 1, 1);
    }
}


@media (max-width:800px) {
  .mainnews{flex-direction: column;max-height: 700px;}
  .newspics{width: 100%;margin-right: 0px;border-radius: 5px;}
  .descriptions *{margin: 10px 0px 10px 0px ;}
}